.movie-item{margin-top:8px;margin-bottom:8px;width:49%;background:#fff;-webkit-transition:all .2s linear;transition:all .2s linear}.movie-item.active{box-shadow:0 15px 30px rgba(0,0,0,0.1);-webkit-transform:translate3d(0, -2px, 0);transform:translate3d(0, -2px, 0)}.movie-item .movie-cover{position:relative;width:100%;cursor:pointer}.movie-item .movie-cover .movie-overlay{position:absolute;left:0;top:0;width:100%;height:100%;background:#000;opacity:.5;z-index:2;-webkit-transition:all .2s linear;transition:all .2s linear}.movie-item .movie-cover .play-icon{position:absolute;top:50%;left:50%;z-index:3;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.movie-item .movie-cover img{-webkit-transition:all .2s linear;transition:all .2s linear;max-width:100%;height:auto;margin:0 auto}.movie-item .movie-description{padding-left:8px;padding-right:8px}.movie-item .movie-description h3{margin-top:5px;margin-bottom:5px;font-size:1em;height:26px;line-height:26px;overflow:hidden;text-overflow:ellipsis;text-align:center;font-weight:100}


